  // cameraSelect.onchange = async(event) => {
  //   cameraId = event.target.value;
  //   await qrCodeReader.stop()
  //   await startScanning(event.target.value);
  // }

  // Html5Qrcode.getCameras().then(devices => {
  //   if (devices && devices.length) {
